import React from 'react';
import DemoEffect from './DemoEffect.js'
class App extends React.Component {
	

   constructor(props) {
		
      super();
	  this.state = {
		  rotateCube: true
	  }
      this.forceUpdateHandler = this.forceUpdateHandler.bind(this);
	  this.getState = this.getState.bind(this);
	 
   };
   getState() {
	   return this.state.rotateCube;
   }
   forceUpdateHandler() {
	
      this.forceUpdate();
   };
   

   render() {
      return (
			
			<div>
		
         <div className="navBar">
		
            <button className={this.state.rotateCube ? "button2" : "button1"} onClick = {this.forceUpdateHandler}>{this.state.rotateCube ? "Stop Rotation" : "Start Rotation"} </button>
				{this.state.rotateCube = !this.state.rotateCube}
			</div>
			<div>
				<DemoEffect rotateCube = {this.state.rotateCube}/>
		</div>
   
		 </div>
      );
   }
}

export default App;


